{/*Buttons */}

class Buttons extends React.Component {
  constructor(props) {
    super(props)
  this.state = {
      output: ""
      }
      this.home = this.home.bind(this);
      this.action = this.action.bind(this);
      this.drama = this.drama.bind(this);
      this.adventure = this.adventure.bind(this);
      this.horror = this.horror.bind(this);
      this.comedy = this.comedy.bind(this);
    }
    home(){ this.setState({output: ""})}
    action() { this.setState({output: "Action"}) }
    drama() { this.setState({output: "Drama"}) }
    adventure() { this.setState({output: "Adventure"}) }
    horror() {this.setState({output: "Horror"})}
    comedy() {this.setState({output: "Comedy"})}

    render() {
  return(
    <div className="buttons">
      <nav>
        <input type="checkbox" id="check"/>
        <label for="check" className="checkbtn">
          <div className="categoryLogo"></div>
          <div className="categoryLogo"></div>
          <div className="categoryLogo"></div>
        </label>
        <ul>
      <li><a id="categories" onClick={this.home}><h3>Home</h3></a></li>
      <li><a id="categories" onClick={this.action}><h3>Action</h3></a></li>
      <li><a id="categories" onClick={this.drama}><h3>Drama</h3></a></li>
      <li><a id="categories" onClick={this.adventure}><h3>Adventure</h3></a></li>
      <li><a id="categories" onClick={this.horror}><h3>Horror</h3></a></li>
      <li><a id="categories" onClick={this.comedy}><h3>Comedy</h3></a></li>
      </ul>
      </nav>
      <div id="libraryPos">
        <Library category={this.state.output}/>
      </div>
    </div>
  )
    }
  }

{/*Search Eng */}
class Search extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      input: ""
    }
  }
  handleChange(event) {
    this.setState({
      input: event.target.value
    })
  }
  render() {
    return(
      <div>
        <input className="src" value = {this.state.input} onChange = {this.handleChange.bind(this)}/>
        <SearchResult output={this.state.input}/>
      </div>
    )
  }
}

class SearchResult extends React.Component {
  constructor(props) {
    super(props)
  }
  render() {
    function searchLib(link,src, alt, title, category = []) {
      const categories = category.map(e => <p className="category">{e}</p>)
      return (
        <div className="srcAnim">
          <a href={link}>
        <img style={{width:"25vh"},{height: "20vh"}} src={src} alt={alt} />
        </a>
        <div>
        <h3 className="title" style={{paddingRight:30}}>{title}</h3>
        <div className="categories">
          {categories}
        </div>
        </div>
        </div>
      )
    }
    var searchLi = {
      0: ["naruto", searchLib("https://asenshu.com/seriale/naruto/",naruto, "naruto", "Naruto", ["Action", "Adventure"])],
      1: ["attack on titan", searchLib("https://asenshu.com/seriale/attack-on-titan-ova/",aot, "attackOnTitan", "Attack On Titan",["Action", "Drama", "Adventure"] )],
      2: ["my hero academia", searchLib("https://asenshu.com/seriale/my-hero-academia/",mha, "mhaImg", "My Hero Academia", ["Action", "Drama"])],
      3: ["jujutsu kaisen", searchLib("https://www1.kissanimes.cc/watch-anime/jujutsu-kaisen-tv",jk, "jkImg", "Jujutsu Kaisen",["Action", "Horror"])],
      4: ["one punch man", searchLib("https://asenshu.com/seriale/one-punch-man/",opm, "opmImg", "One Punch Man", ["Action", "Comedy"])],
      5:["the seven deadly sins", searchLib("https://asenshu.com/seriale/the-seven-deadly-sins/",sevenDeadly, "sdsImg", "The Seven Deadly Sins", ["Action", "Drama"])],
      6: ["dragon ball", searchLib("https://asenshu.com/seriale/dragon-ball-z-kai/",db, "dbImg", "Dragon Ball"["Action"])],
      7: ["kakeguri", searchLib("https://gototub.com/series/kakegurui/",kakegur, "kakeguri", "Kakeguri",["Drama"])],
      8: ["fate", searchLib("https://www.justwatch.com/us/tv-show/fate-stay-night-unlimited-blade-works",fsn, "fateStayNight", "Fate/StayNight: Ultimated Blade Works", ["Action"])],
      9: ["devil man", searchLib("https://ww3.9movies.yt/watch/devilman-crybaby-19218/?ep=45018",devilM, "devilMan", "DevilMan Crybaby",["Action", "Adventure"])],
      10: ["sword art online", searchLib("https://watch-sao.com/",sao, "saoImg", "Sword Art Online", ["Action", "Adventure"])],
      11: ["miraculous", searchLib("https://www.playpilot.com/uk/show/miraculous-tales-of-ladybug-cat/", miraculous, "mirImg", "Miraculous: Tales Of Ladybug & Cat Noir (Webisodes)", ["Action", "Comedy"] )],
      12: ["violet", searchLib("https://gogoanime.link/series/violet-evergarden-sub-eng/oAAxZkPv/VqWAUcg3/AZbYUBFu-watch-free.html", violete, "violeteImg", "Violet Evergarden", ["Drama"])],
      13: ["jojo", searchLib("https://www.crunchyroll.com/jojos-bizarre-adventure", jojo, "jojoImg", "JoJo's Bizarre Adventures", ["Adventure"])],
      14: ["death", searchLib("https://ww1.deathnoteepisodes.com/death-note-episode-1-english-subbed-watch-online/", death, "dnImg", "Death Note", ["Drama","Horror"])],
      15: ["charlotte", searchLib("https://www.crunchyroll.com/charlotte", char, "Charlotte", "Charlotte", ["Drama"])],
      16: ["k", searchLib("https://animelon.com/series/K:%20Return%20of%20Kings", kk, "k", "K", ["Action"])],
      17: ["sirius", searchLib("https://www.betaseries.com/en/episode/tenrou-sirius-the-jaeger/s01e01", sirius, "sirius", "Sirius The Jaeger",["Horror", "Action", "Adventure"])],
      18: ["dodoro", searchLib("https://www5.gogoanime.pro/anime/dororo-y7nx", dor, "dor", "Dodoro", ["Action", "Adventure"])],
      19: ["psycho", searchLib("https://www.animelab.com/shows/psychopass", psycho, "psyco", "Psycho-pass"["Action", "Drama"])],
      20: ["great", searchLib("https://www26.gogoanimes.tv/great-pretender-episode-1", great , "great", "Great Pretender", ["Comedy"])],
      21: ["dragons", searchLib("https://123moviesca.com/series/dragons-dogma/", dragons, "dragons", "Dragon's Dogma", ["Adventure"])],
      22: ["hero", searchLib("https://www1.zoechip.com/tv/hero-mask-35182", hero, "heroImg", "Hero Mask", ["Action"])],
      23: ["b", searchLib("https://www.anime-planet.com/anime/b-the-beginning", b, "b", "B: The Beginning"["Action"])]
  }
  var display = []
  for(let i = 0; i <= 23; i++) {
    if(searchLi[i][0] === this.props.output) {
      display.push(searchLi[i][1])
    }
  }
    return(
      <div className="searchOutput">
        <p style={{display: "none"}}>{this.props.output}</p>
        {display}
      </div>
    )
  }
}


{/*Library*/}

class Library extends React.Component {
  constructor (props) {
    super(props)
  }
  
  
  render(){
    function libraryStructure(link,id, src, alt, cat=[], title) {
      
      const categories = cat.map(event=> <p className="category">{event}</p>)
      return(<div className="anime" id={id}>
        <a href={link} target="_blank">
        <img id="libImg" src={src} alt={alt} />
        <div className="categories">
          {categories}
        </div>
        <h3 className="title">{title}</h3>
        </a>
      </div>)
    }
    let nar = libraryStructure("https://asenshu.com/seriale/naruto/","Naruto", naruto, "Naruto", ["Action", "Adventure"], "Naruto");
  let attackOnTitan = libraryStructure("https://asenshu.com/seriale/attack-on-titan-ova/","aot", aot, "attackOnTitan", ["Action", "Drama", "Adventure"], "Attack On Titan");
  let myHeroAcademia = libraryStructure("https://asenshu.com/seriale/my-hero-academia/","mha", mha, "myHeroAcademy", ["Action", "Drama"], "My Hero Academia");
  let jujutsuKaisen = libraryStructure("https://www1.kissanimes.cc/watch-anime/jujutsu-kaisen-tv","jk", jk, "jujutsu kaisen", ["Action", "Horror"], "Jujutsu Kaisen");
  let onePunchMan = libraryStructure("https://asenshu.com/seriale/one-punch-man/","opm", opm, "One Punch Man", ["Action", "Comedy"], "One Punch Man")
  let theSevenDeadlySins = libraryStructure("https://asenshu.com/seriale/the-seven-deadly-sins/","sds", sevenDeadly, "theSevenDeadlySins", ["Action", "Drama"], "The Seven Deadly Sins");
  let dragonBall = libraryStructure("https://asenshu.com/seriale/dragon-ball-z-kai/","db", db, "DragonBall", ["Action", "Adventure"], "Dragon Ball");
  let kakeguri = libraryStructure("https://gototub.com/series/kakegurui/","kakeguri", kakegur, "Kakeguri", ["Drama"], "Kakeguri");
  let fsnvar = libraryStructure("https://www.justwatch.com/us/tv-show/fate-stay-night-unlimited-blade-works","fsn", fsn, "fsn", ["Action"], "Fate/Stay Night: Ultimated Blade Works");
  let devilMan = libraryStructure("https://ww3.9movies.yt/watch/devilman-crybaby-19218/?ep=45018","devilM", devilM, "devilMan", ["Action", "Adventure"], "Devilman Crybaby");
  let swordArtOnline = libraryStructure("https://watch-sao.com/","sao", sao, "saoImg", ["Action", "Adventure"], "Sword Art Online");
  let miriculou = libraryStructure("https://www.playpilot.com/uk/show/miraculous-tales-of-ladybug-cat/", "mir", miraculous, "mirImg", ["Action", "Comedy"], "Miraculous: Tales Of Ladybug & Cat Noir (Webisodes)");
  let violetEvergarden = libraryStructure("https://gogoanime.link/series/violet-evergarden-sub-eng/oAAxZkPv/VqWAUcg3/AZbYUBFu-watch-free.html","violet", violete, "violeteImg", ["drama"], "Violete Evergarden");
  let jojosBizarreAdventures = libraryStructure("https://www.crunchyroll.com/jojos-bizarre-adventure","jojo", jojo, "jojoImg",["Adventure"],"Jojo's Bizarre Adventure");
  let deathNote = libraryStructure("https://ww1.deathnoteepisodes.com/death-note-episode-1-english-subbed-watch-online/", "dn", death, "dnImg", ["Drama","Horror"], "Death Note");
  let charlotte = libraryStructure("https://www.crunchyroll.com/charlotte", "charlotte", char, "charlotte", ["Drama"], "Charlotte");
  let k = libraryStructure("https://animelon.com/series/K:%20Return%20of%20Kings","k", kk, "k", ["Action"], "K");
  let siriusTheJaeger = libraryStructure("https://www.betaseries.com/en/episode/tenrou-sirius-the-jaeger/s01e01", "sirius", sirius, "sirius", ["Action", "Adventure", "Horror"], "Sirius The Jaeger");
  let dodoro = libraryStructure("https://www5.gogoanime.pro/anime/dororo-y7nx", "dor", dor , "dor", ["Action", "Adventure"], "Dodoro");
  let psychoPass = libraryStructure("https://www.animelab.com/shows/psychopass", "psycho", psycho, "psycho", ["Action", "Drama"], "Psycho-Pass");
  let greatPretender = libraryStructure("https://www26.gogoanimes.tv/great-pretender-episode-1", "great", great, "great", ["Comedy"], "Great Pretender");
  let dragonsDogma = libraryStructure("https://123moviesca.com/series/dragons-dogma/", "dragons", dragons, "dragons", ["Adventure"], "Dragon's Dogma")
  let heroMask = libraryStructure("https://www1.zoechip.com/tv/hero-mask-35182", "hero", hero, "hero", ["Action"], "Hero Mask");
  let bTheBeginning = libraryStructure("https://www.anime-planet.com/anime/b-the-beginning", "b", b, "b", ["Action"], "B: The Beginning");

  var action = [nar, attackOnTitan, myHeroAcademia, jujutsuKaisen, onePunchMan, theSevenDeadlySins, dragonBall, fsnvar, devilMan, swordArtOnline, miriculou, k, siriusTheJaeger, dodoro, psychoPass, heroMask, bTheBeginning];
  var adventure = [nar, attackOnTitan, devilMan, swordArtOnline, jojosBizarreAdventures, siriusTheJaeger, dodoro, psychoPass, dragonsDogma];
  var drama = [attackOnTitan, myHeroAcademia, kakeguri, devilMan, violetEvergarden, deathNote, charlotte];
  var home = [nar, attackOnTitan, myHeroAcademia, jujutsuKaisen, onePunchMan, theSevenDeadlySins, dragonBall, kakeguri, fsnvar, devilMan, swordArtOnline, miriculou, violetEvergarden, jojosBizarreAdventures, deathNote, charlotte, k, siriusTheJaeger, dodoro, psychoPass, greatPretender, dragonsDogma, heroMask, bTheBeginning];
  var comedy = [miriculou, onePunchMan, greatPretender];
  var horror = [jujutsuKaisen, deathNote, siriusTheJaeger];
  var display;
  if(this.props.category === "Action") {
    display = action;
  } else if(this.props.category === "Adventure") {
    display = adventure;
  } else if (this.props.category === "Drama") {
    display = drama;
  } else if (this.props.category === "Comedy") {
    display = comedy;
  } else if (this.props.category === "Horror") {
    display = horror;
  }
  else {
    display = home;
  }
      return(
        <div id="library">
          {display}
          <p style={{display: "none"}}>{this.props.category}</p>
        </div>
      )
  }
}

export default App;